// 诗词
let poetry = [
    {id : "71",title: "临江仙·闺思 史达祖" , content : "愁与西风应有约，年年同赴清秋。旧游帘幕记扬州。一灯人著梦，双燕月当楼。罗带鸳鸯尘暗澹，更须整顿风流。天涯万一见温柔。瘦应因此瘦，羞亦为郎羞。", timedate : "2023-04-26 8:49"},
    {id : "70",title: "水调歌头·壬子三山被召陈端仁给事饮饯席上作 白玉蟾" , content : "长恨复长恨，裁作短歌行。何人为我楚舞，听我楚狂声？余既滋兰九畹，又树蕙之百亩，秋菊更餐英。门外沧浪水，可以濯吾缨。一杯酒，问何似，身后名？人间万事，毫发常重泰山轻。悲莫悲生离别，乐莫乐新相识，儿女古今情。富贵非吾事，归与白鸥盟。", timedate : "2023-04-26 8:49"},
    {id : "69",title: "华阳堂二咏 白玉蟾" , content : "不著人间一点尘，满堂尽是学仙人。衣衫总带烟霞色，杖屦相随云水身。铁笛横吹沧海月，纸袍包尽洞天春。而今会聚十方客，认看何人是洞宾。", timedate : "2023-04-26 8:49"},
    {id : "68",title: "一剪梅·雨打梨花深闭门 唐寅" , content : "雨打梨花深闭门，辜负青春，虚负青春。赏心乐事共谁论？花下销魂，月下销魂。愁聚眉峰尽日颦，千点啼痕，万点啼痕。晓看天色暮看云，行也思君，坐也思君。", timedate : "2023-04-26 8:49"},
    {id : "67",title: "沈园二首·其一 陆游" , content : "城上斜阳画角哀，沈园非复旧池台。伤心桥下春波绿，曾是惊鸿照影来。", timedate : "2023-04-26 8:49"},
    {id : "66",title: "长干行二首 李白" , content : "妾发初覆额，折花门前剧。郎骑竹马来，绕床弄青梅。同居长干里，两小无嫌猜，十四为君妇，羞颜未尝开。低头向暗壁，千唤不一回。十五始展眉，愿同尘与灰。常存抱柱信，岂上望夫台。十六君远行，瞿塘滟滪堆。五月不可触，猿声天上哀。门前迟行迹，一一生绿苔。苔深不能扫，落叶秋风早。八月蝴蝶黄，双飞西园草。感此伤妾心，坐愁红颜老。早晚下三巴，预将书报家。相迎不道远，直至长风沙。", timedate : "2023-03-20 11:22"},
    {id : "65",title: "扬州慢·淮左名都 姜夔" , content : "淮左名都，竹西佳处，解鞍少驻初程。过春风十里，尽荠麦青青。自胡马窥江去后，废池乔木，犹厌言兵。渐黄昏，清角吹寒，都在空城。杜郎俊赏，算而今，重到须惊。纵豆蔻词工，青楼梦好，难赋深情。二十四桥仍在，波心荡，冷月无声。念桥边红药，年年知为谁生？", timedate : "2023-03-20 11:22"},
    {id : "64",title: "梅花引·荆溪阻雪 蒋捷" , content : "白鸥问我泊孤舟，是身留，是心留？心若留时，何事锁眉头？心拍小帘灯晕舞，对闲影，冷清清，忆旧游。旧游旧游今在否？花外楼，柳下舟。梦也梦也，梦不到，寒水空流。漠漠黄云，湿透木棉裘。都道无人愁似我，今夜雪，有梅花，似我愁。", timedate : "2023-03-20 11:20"},
    {id : "63",title: "赠卫八处士 杜甫" , content : "人生不相见，动如参与商。今夕复何夕，共此灯烛光。少壮能几时，鬓发各已苍。访旧半为鬼，惊呼热中肠。焉知二十载，重上君子堂。昔别君未婚，儿女忽成行。怡然敬父执，问我来何方。问答乃未已，驱儿罗酒浆。夜雨剪春韭，新炊间黄粱。主称会面难，一举累十觞。十觞亦不醉，感子故意长。明日隔山岳，世事两茫茫。", timedate : "2023-03-20 11:15"},
    {id : "62",title: "沁园春·将止酒戒酒杯使勿近 辛弃疾" , content : "杯汝来前！老子今朝，点检形骸。甚长年抱渴，咽如焦釜；于今喜睡，气似奔雷。汝说“刘伶，古今达者，醉后何妨死便埋”。浑如此，叹汝于知己，真少恩哉。更凭歌舞为媒。算合作人间鸩毒猜。况怨无小大，生于所爱；物无美恶，过则为灾。与汝成言，勿留亟退，吾力犹能肆汝杯。杯再拜，道麾之即去，招则须来。", timedate : "2023-02-03 20:37"},
    {id : "61",title: "木兰花令·次马中玉韵 苏轼" , content : "知君仙骨无寒暑，千载相逢犹旦暮。故将别语恼佳人，与看梨花枝上雨。落花已逐回风去，花本无心莺自诉。明朝归路下塘西，不见莺啼花落处", timedate : "2023-02-03 20:37"},
    {id : "60",title: "司马将军歌 李白" , content : "狂风吹日月，窃弄章华台。北落明星动光彩，南征猛将云如雷。手中电曳倚天剑，只斩长鲸海水开。我见楼船壮心目，颇似龙骧下三蜀。扬兵习战张虎旗，江中白浪似银屋。身居玉帐临河魁，紫髯若戟冠崔嵬，细柳开营揖天子，始知灞上为婴孩。羌笛横吹阿亸回，向月楼中吹落梅。将军自起舞长剑，壮士呼声动九垓。功成献凯见明主，丹青画像麒麟台。", timedate : "2023-02-03 20:37"},
    {id : "59",title: "临江仙·身外闲愁空满 晏几道" , content : "身外闲愁空满，眼中欢事常稀。明年应赋送君诗。细从今夜数，相会几多时。浅酒欲邀谁劝，深情惟有君知。东溪春近好同归。柳垂江上影，梅谢雪中枝。", timedate : "2021-11-16 20:37"},
    {id : "58",title: "小戎 佚名" , content : "小戎俴收，五楘梁辀。游环胁驱，阴靷鋈续。文茵畅毂，驾我骐馵。言念君子，温其如玉。在其板屋，乱我心曲。四牡孔阜，六辔在手。骐骝是中，騧骊是骖。龙盾之合，鋈以觼軜。言念君子，温其在邑。方何为期？胡然我念之。俴驷孔群，厹矛鋈錞。蒙伐有苑，虎韔镂膺。交韔二弓，竹闭绲滕。言念君子，载寝载兴。厌厌良人，秩秩德音。", timedate : "2021-11-16 20:37"},
    {id : "57",title: "无俗念·灵虚宫梨花辞 丘处机" , content : "春游浩荡，是年年寒食，梨花时节。白锦无纹香烂漫，玉树琼苞堆雪。静夜沉沉，浮光霭霭，冷浸溶溶月。人间天上，烂银霞照通彻。浑似姑射真人，天姿灵秀，意气舒高洁。万蕊参差谁信道，不与群芳同列。浩气清英，仙材卓荦，下土难分别。瑶台归去，洞天方看清绝。", timedate : "2021-11-16 20:37"},
    {id : "56",title: "行香子·述怀 苏轼" , content : "清夜无尘，月色如银。酒斟时、须满十分。浮名浮利，虚苦劳神。叹隙中驹，石中火，梦中身。虽抱文章，开口谁亲。且陶陶、乐尽天真。几时归去，作个闲人。对一张琴，一壶酒，一溪云。", timedate : "2021-11-16 20:37"},
    {id : "55",title: "登金陵凤凰台 李白" , content : "凤凰台上凤凰游，凤去台空江自流。吴宫花草埋幽径，晋代衣冠成古丘。三山半落青天外，二水中分白鹭洲。总为浮云能蔽日，长安不见使人愁。", timedate : "2021-11-16 20:37"},
    {id : "54",title: "把酒问月·故人贾淳令予问之 李白" , content : "青天有月来几时？我今停杯一问之。人攀明月不可得，月行却与人相随。皎如飞镜临丹阙，绿烟灭尽清辉发。但见宵从海上来，宁知晓向云间没。白兔捣药秋复春，嫦娥孤栖与谁邻？今人不见古时月，今月曾经照古人。古人今人若流水，共看明月皆如此。唯愿当歌对酒时，月光长照金樽里。", timedate : "2021-11-16 20:37"},
    {id : "53",title: "十一月二日风雨大作 陆游" , content : "风卷江湖雨暗村，四山声作海涛翻。溪柴火软蛮毡暖，我与狸奴不出门。僵卧孤村不自哀，尚思为国戍轮台。夜阑卧听风吹雨，铁马冰河入梦来。", timedate : "2021-11-16 20:37"},
    {id : "52",title: "醉后却寄元九 白居易" , content : "蒲池村里悤悤别，沣水桥边兀兀回。行到城门残酒醒，万重离恨一时来。", timedate : "2021-11-16 20:37"},
    {id : "51",title: "井底引银瓶·止淫奔也 白居易" , content : "井底引银瓶，银瓶欲上丝绳绝。石上磨玉簪，玉簪欲成中央折。瓶沉簪折知奈何？似妾今朝与君别。忆昔在家为女时，人言举动有殊姿。婵娟两鬓秋蝉翼，宛转双蛾远山色。笑随戏伴后园中，此时与君未相识。妾弄青梅凭短墙，君骑白马傍垂杨。墙头马上遥相顾，一见知君即断肠。知君断肠共君语，君指南山松柏树。感君松柏化为心，暗合双鬟逐君去。到君家舍五六年，君家大人频有言。聘则为妻奔是妾，不堪主祀奉蘋蘩。终知君家不可住，其奈出门无去处。岂无父母在高堂？亦有亲情满故乡。潜来更不通消息，今日悲羞归不得。为君一日恩，误妾百年身。寄言痴小人家女，慎勿将身轻许人！", timedate : "2021-11-16 20:37"},
    {id : "50",title: "清平乐·别来春半 李煜" , content : "别来春半，触目柔肠断。砌下落梅如雪乱，拂了一身还满。雁来音信无凭，路遥归梦难成。离恨恰如春草，更行更远还生。", timedate : "2021-11-16 20:37"},
    {id : "49",title: "南乡子·自古帝王州 王安石" , content : "自古帝王州，郁郁葱葱佳气浮。四百年来成一梦，堪愁。晋代衣冠成古丘。绕水恣行游。上尽层城更上楼。往事悠悠君莫问，回头。槛外长江空自流。", timedate : "2021-11-16 20:37"},
    {id : "48",title: "洞仙歌·冰肌玉骨 苏轼" , content : "冰肌玉骨，自清凉无汗。水殿风来暗香满。绣帘开，一点明月窥人，人未寝，欹枕钗横鬓乱。起来携素手，庭户无声，时见疏星渡河汉。试问夜如何？夜已三更，金波淡，玉绳低转。但屈指西风几时来，又不道流年暗中偷换。", timedate : "2021-11-16 20:37", remark: '仆七岁时，见眉州老尼，姓朱，忘其名，年九十岁。自言尝随其师入蜀主孟昶宫中，一日大热，蜀主与花蕊夫人夜纳凉摩诃池上，作一词，朱具能记之。今四十年，朱已死久矣，人无知此词者，但记其首两句，暇日寻味，岂《洞仙歌》令乎？乃为足之云。'},
    {id : "47",title: "诀别书 卓文君" , content : "春华竞芳，五色凌素，琴尚在御，而新声代故！锦水有鸳，汉宫有木，彼物而新，嗟世之人兮，瞀于淫而不悟！朱弦断，明镜缺，朝露晞，芳时歇，白头吟，伤离别，努力加餐勿念妾，锦水汤汤，与君长诀！", timedate : "2021-11-16 20:37"},
    {id : "46",title: "鹧鸪天·西都作 朱敦儒" , content : "我是清都山水郎，天教分付与疏狂。曾批给雨支风券，累上留云借月章。诗万首，酒千觞。几曾着眼看侯王？玉楼金阙慵归去，且插梅花醉洛阳。", timedate : "2021-11-16 20:37"},
    {id : "45",title: "蝶恋花·阅尽天涯别离苦 王国维" , content : "阅尽天涯离别苦，不道归来，零落花如许。花底相看无一语，绿窗春与天俱莫。待把相思灯下诉，一缕新欢，旧恨千千缕。最是人间留不住，朱颜辞镜花辞树。", timedate : "2021-11-16 20:37"},
    {id : "44",title: "自题金山画像 苏轼" , content : "心似已灰之木，身如不系之舟。问汝平生功业，黄州惠州儋州。", timedate : "2021-11-16 20:37"},
    {id : "43",title: "四块玉·闲适 关汉卿" , content : "适意行，安心坐，渴时饮，饥时餐，醉时歌，困来时就向莎茵卧。日月长，天地阔，闲快活！旧酒投，新醅泼，老瓦盆边笑呵呵，共山僧野叟闲吟和。他出一对鸡，我出一个鹅，闲快活！意马收，心猿锁，跳出红尘恶风波，槐阴午梦谁惊破？离了利名场，钻入安乐窝，闲快活！南亩耕，东山卧，世态人情经历多，闲将往事思量过。贤的是他，愚的是我，争甚么？", timedate : "2021-11-16 20:37"},
    {id : "42",title: "行香子 明本" , content : "玉殿琼楼。金锁银钩。总不如、岩谷清幽。蒲团纸帐，瓦钵磁瓯。却不知春，不知夏，不知秋。万事俱休。名利都勾。翼攀缘、永绝追求。溪山作伴，云月为俦。但乐清闲，乐自在，乐优游。", timedate : "2021-11-16 20:37"},
    {id : "41",title: "南歌子·凤髻金泥带 欧阳修" , content : "凤髻金泥带，龙纹玉掌梳。走来窗下笑相扶，爱道画眉深浅入时无？弄笔偎人久，描花试手初。等闲妨了绣功夫，笑问鸳鸯两字怎生书？", timedate : "2021-11-16 20:37"},
    {id : "40",title: "梦江南·千万恨 温庭筠" , content : "千万恨，恨极在天涯。山月不知心里事，水风空落眼前花，摇曳碧云斜。", timedate : "2021-11-16 20:37"},
    {id : "39",title: "酌酒与裴迪 王维" , content : "酌酒与君君自宽，人情翻覆似波澜。白首相知犹按剑，朱门先达笑弹冠。草色全经细雨湿，花枝欲动春风寒。世事浮云何足问，不如高卧且加餐。", timedate : "2021-11-16 20:37"},
    {id : "38",title: "鹧鸪天·建康上元作 赵鼎" , content : "客路那知岁序移，忽惊春到小桃枝。天涯海角悲凉地，记得当年全盛时。花弄影，月流辉，水晶宫殿五云飞。分明一觉华胥梦，回首东风泪满衣。", timedate : "2021-11-16 20:37"},
    {id : "37",title: "钗头凤·世情薄 唐婉" , content : "世情薄，人情恶，雨送黄昏花易落。晓风干，泪痕残。欲笺心事，独语斜阑。难，难，难！人成各，今非昨，病魂常似秋千索。角声寒，夜阑珊。怕人寻问，咽泪装欢。瞒，瞒，瞒！", timedate : "2021-11-16 20:37"},
    {id : "36",title: "江城子·江景 苏轼" , content : "湖上与张先同赋，时闻弹筝凤凰山下雨初晴，水风清，晚霞明。一朵芙蕖，开过尚盈盈。何处飞来双白鹭，如有意，慕娉婷。忽闻江上弄哀筝，苦含情，遣谁听！烟敛云收，依约是湘灵。欲待曲终寻问取，人不见，数峰青。", timedate : "2021-11-16 20:37"},
    {id : "35",title: "四愁诗 张衡" , content : "我所思兮在太山，欲往从之梁父艰。侧身东望涕沾翰。美人赠我金错刀，何以报之英琼瑶。路远莫致倚逍遥，何为怀忧心烦劳。我所思兮在桂林，欲往从之湘水深。侧身南望涕沾襟。美人赠我琴琅玕，何以报之双玉盘。路远莫致倚惆怅，何为怀忧心烦怏。我所思兮在汉阳，欲往从之陇阪长。侧身西望涕沾裳。美人赠我貂襜褕，何以报之明月珠。路远莫致倚踟蹰，何为怀忧心烦纡。我所思兮在雁门，欲往从之雪雰雰。侧身北望涕沾巾。美人赠我锦绣段，何以报之青玉案。路远莫致倚增叹，何为怀忧心烦惋。", timedate : "2021-11-16 20:37"},
    {id : "34",title: "经离乱后天恩流夜郎忆旧游书怀赠江夏韦太守良宰 李白" , content : "天上白玉京，十二楼五城。仙人抚我顶，结发受长生。误逐世间乐，颇穷理乱情。九十六圣君，浮云挂空名。天地赌一掷，未能忘战争。试涉霸王略，将期轩冕荣。时命乃大谬，弃之海上行。学剑翻自哂，为文竟何成。剑非万人敌，文窃四海声。儿戏不足道，五噫出西京。临当欲去时，慷慨泪沾缨。叹君倜傥才，标举冠群英。开筵引祖帐，慰此远徂征。鞍马若浮云，送余骠骑亭。歌钟不尽意，白日落昆明。十月到幽州，戈鋋若罗星。君王弃北海，扫地借长鲸。呼吸走百川，燕然可摧倾。心知不得语，却欲栖蓬瀛。弯弧惧天狼，挟矢不敢张。揽涕黄金台，呼天哭昭王。无人贵骏骨，騄耳空腾骧。乐毅倘再生，于今亦奔亡。蹉跎不得意，驱马还贵乡。逢君听弦歌，肃穆坐华堂。百里独太古，陶然卧羲皇。征乐昌乐馆，开筵列壶觞。贤豪间青娥，对烛俨成行。醉舞纷绮席，清歌绕飞梁。欢娱未终朝，秩满归咸阳。祖道拥万人，供帐遥相望。一别隔千里，荣枯异炎凉。炎凉几度改，九土中横溃。汉甲连胡兵，沙尘暗云海。草木摇杀气，星辰无光彩。白骨成丘山，苍生竟何罪。函关壮帝居，国命悬哥舒。长戟三十万，开门纳凶渠。公卿如犬羊，忠谠醢与菹。二圣出游豫，两京遂丘墟。帝子许专征，秉旄控强楚。节制非桓文，军师拥熊虎。人心失去就，贼势腾风雨。惟君固房陵，诚节冠终古。仆卧香炉顶，餐霞漱瑶泉。门开九江转，枕下五湖连。半夜水军来，浔阳满旌旃。空名适自误，迫胁上楼船。徒赐五百金，弃之若浮烟。辞官不受赏，翻谪夜郎天。夜郎万里道，西上令人老。扫荡六合清，仍为负霜草。日月无偏照，何由诉苍昊。良牧称神明，深仁恤交道。一忝青云客，三登黄鹤楼。顾惭祢处士，虚对鹦鹉洲。樊山霸气尽，寥落天地秋。江带峨眉雪，川横三峡流。万舸此中来，连帆过扬州。送此万里目，旷然散我愁。纱窗倚天开，水树绿如发。窥日畏衔山，促酒喜得月。吴娃与越艳，窈窕夸铅红。呼来上云梯，含笑出帘栊。对客小垂手，罗衣舞春风。宾跪请休息，主人情未极。览君荆山作，江鲍堪动色。清水出芙蓉，天然去雕饰。逸兴横素襟，无时不招寻。朱门拥虎士，列戟何森森。剪凿竹石开，萦流涨清深。登台坐水阁，吐论多英音。片辞贵白璧，一诺轻黄金。谓我不愧君，青鸟明丹心。五色云间鹊，飞鸣天上来。传闻赦书至，却放夜郎回。暖气变寒谷，炎烟生死灰。君登凤池去，忽弃贾生才。桀犬尚吠尧，匈奴笑千秋。中夜四五叹，常为大国忧。旌旆夹两山，黄河当中流。连鸡不得进，饮马空夷犹。安得羿善射，一箭落旄头。", timedate : "2021-11-16 20:37"},
    {id : "33",title: "唐多令·芦叶满汀洲 刘过" , content : "安远楼小集，侑觞歌板之姬黄其姓者，乞词于龙洲道人，为赋此《唐多令》。同柳阜之、刘去非、石民瞻、周嘉仲、陈孟参、孟容。时八月五日也。芦叶满汀洲，寒沙带浅流。二十年重过南楼。柳下系船犹未稳，能几日，又中秋。黄鹤断矶头，故人今在否？旧江山浑是新愁。欲买桂花同载酒，终不似，少年游。", timedate : "2021-11-16 20:37"},
    {id : "32",title: "金缕曲词二首·其二 顾贞观" , content : "我亦飘零久！十年来，深恩负尽，死生师友。宿昔齐名非忝窃，只看杜陵消瘦。曾不减，夜郎僝僽。薄命长辞知己别，问人生到此凄凉否？千万恨，为君剖。兄生辛未吾丁丑，共此时，冰霜摧折，早衰蒲柳。诗赋从今须少作，留取心魄相守。但愿得，河清人寿！归日急翻行戍稿，把空名料理传身后。言不尽，观顿首。", timedate : "2021-11-16 20:37"},
    {id : "31",title: "浪淘沙·把酒祝东风 欧阳修" , content : "把酒祝东风，且共从容。垂杨紫陌洛城东。总是当时携手处，游遍芳丛。聚散苦匆匆，此恨无穷。今年花胜去年红。可惜明年花更好，知与谁同？", timedate : "2021-11-16 20:37"},
    {id : "30",title: "生查子·元夕 欧阳修" , content : "去年元夜时，花市灯如昼。月上柳梢头，人约黄昏后。今年元夜时，月与灯依旧。不见去年人，泪湿春衫袖。", timedate : "2021-11-16 20:37"},
    {id : "29",title: "长歌行 李泌" , content : "天覆吾，地载吾，天地生吾有意无。不然绝粒升天衢，不然鸣珂游帝都。焉能不贵复不去，空作昂藏一丈夫。一丈夫兮一丈夫，千生气志是良图。请君看取百年事，业就扁舟泛五湖。", timedate : "2021-11-16 20:37"},
    {id : "28",title: "贺新郎·同父见和再用韵答之 辛弃疾" , content : "老大那堪说。似而今、元龙臭味，孟公瓜葛。我病君来高歌饮，惊散楼头飞雪。笑富贵千钧如发。硬语盘空谁来听？记当时、只有西窗月。重进酒，换鸣瑟。事无两样人心别。问渠侬：神州毕竟，几番离合？汗血盐车无人顾，千里空收骏骨。正目断关河路绝。我最怜君中宵舞，道“男儿到死心如铁”。看试手，补天裂。", timedate : "2021-11-16 20:37"},
    {id : "27",title: "三五七言 李白" , content : "秋风清，秋月明，落叶聚还散，寒鸦栖复惊。相思相见知何日？此时此夜难为情！入我相思门，知我相思苦，长相思兮长相忆，短相思兮无穷极，早知如此绊人心，何如当初莫相识。", timedate : "2021-11-16 20:37"},
    {id : "26",title: "苏幕遮·怀旧 范仲淹" , content : "碧云天，黄叶地，秋色连波，波上寒烟翠。山映斜阳天接水，芳草无情，更在斜阳外。黯乡魂，追旅思，夜夜除非，好梦留人睡。明月楼高休独倚，酒入愁肠，化作相思泪。", timedate : "2021-11-16 20:37"},
    {id : "25",title: "钗头凤·红酥手 陆游" , content : "红酥手，黄縢酒，满城春色宫墙柳。东风恶，欢情薄。一怀愁绪，几年离索。错、错、错。春如旧，人空瘦，泪痕红浥鲛绡透。桃花落，闲池阁。山盟虽在，锦书难托。莫、莫、莫！", timedate : "2021-11-16 20:37"},
    {id : "24",title: "江楼吹笛饮酒大醉中作 陆游" , content : "世言九州外，复有大九州。此言果不虚，仅可容吾愁。许愁亦当有许酒，吾酒酿尽银河流。酌之万斛玻璃舟，酣宴五城十二楼。天为碧罗幕，月作白玉钩;织女织庆云，裁成五色裘。披裘对酒难为客，长揖北辰相献酬。一饮五百年，一醉三千秋。却驾白风骖斑虯，下与麻姑戏玄洲。锦江吹笛余一念，再过剑南应小留。", timedate : "2021-11-16 20:37"},
    {id : "23",title: "春江花月夜 张若虚" , content : "春江潮水连海平，海上明月共潮生。滟滟随波千万里，何处春江无月明！江流宛转绕芳甸，月照花林皆似霰。空里流霜不觉飞，汀上白沙看不见。江天一色无纤尘，皎皎空中孤月轮。江畔何人初见月？江月何年初照人？人生代代无穷已，江月年年望相似。不知江月待何人，但见长江送流水。白云一片去悠悠，青枫浦上不胜愁。谁家今夜扁舟子？何处相思明月楼？可怜楼上月裴回，应照离人妆镜台。玉户帘中卷不去，捣衣砧上拂还来。此时相望不相闻，愿逐月华流照君。鸿雁长飞光不度，鱼龙潜跃水成文。昨夜闲潭梦落花，可怜春半不还家。江水流春去欲尽，江潭落月复西斜。斜月沉沉藏海雾，碣石潇湘无限路。不知乘月几人归，落月摇情满江树。", timedate : "2021-11-16 20:37"},
    {id : "22",title: "临江仙·夜归临皋 苏轼" , content : "夜饮东坡醒复醉，归来仿佛三更。家童鼻息已雷鸣。敲门都不应，倚杖听江声。长恨此身非我有，何时忘却营营。夜阑风静縠纹平。小舟从此逝，江海寄余生", timedate : "2021-11-16 20:37"},
    {id : "21",title: "摽有梅 佚名" , content : "摽有梅，其实七兮。求我庶士，迨其吉兮。摽有梅，其实三兮。求我庶士，迨其今兮。摽有梅，顷筐塈之。求我庶士，迨其谓之。", timedate : "2021-11-16 20:37"},
    {id : "20",title: "梦微之 白居易" , content : "夜来携手梦同游，晨起盈巾泪莫收。漳浦老身三度病，咸阳宿草八回秋。君埋泉下泥销骨，我寄人间雪满头。阿卫韩郎相次去，夜台茫昧得知不？", timedate : "2021-11-16 20:37"},
    {id : "19",title: "望江南·梳洗罢 温庭筠" , content : "梳洗罢，独倚望江楼。过尽千帆皆不是，斜晖脉脉水悠悠。肠断白蘋洲。", timedate : "2021-11-16 20:37"},
    {id : "18",title: "献钱尚父 贯休" , content : "贵逼人来不自由，龙骧凤翥势难收。满堂花醉三千客，一剑霜寒十四州。鼓角揭天嘉气冷，风涛动地海山秋。东南永作金天柱，谁羡当时万户侯。", timedate : "2021-11-16 20:37"},
    {id : "17",title: "蝶恋花·槛菊愁烟兰泣露 晏殊" , content : "槛菊愁烟兰泣露，罗幕轻寒，燕子双飞去。明月不谙离恨苦，斜光到晓穿朱户。昨夜西风凋碧树，独上高楼，望尽天涯路。欲寄彩笺兼尺素，山长水阔知何处？", timedate : "2021-11-16 20:37"},
    {id : "16",title: "凤栖梧·伫倚危楼风细细 柳永" , content : "伫倚危楼风细细，望极春愁，黯黯生天际。草色烟光残照里，无言谁会凭阑意。拟把疏狂图一醉，对酒当歌，强乐还无味。衣带渐宽终不悔，为伊消得人憔悴。", timedate : "2021-11-16 20:37"},
    {id : "15",title: "定风波·莫听穿林打叶声 苏轼" , content : "三月七日，沙湖道中遇雨，雨具先去，同行皆狼狈，余独不觉。已而遂晴，故作此(词)。莫听穿林打叶声，何妨吟啸且徐行。竹杖芒鞋轻胜马，谁怕？一蓑烟雨任平生。料峭春风吹酒醒，微冷，山头斜照却相迎。回首向来萧瑟处，归去，也无风雨也无晴。", timedate : "2021-11-16 20:37"},
    {id : "14",title: "相见欢·无言独上西楼 李煜" , content : "无言独上西楼，月如钩。寂寞梧桐深院锁清秋。剪不断，理还乱，是离愁。别是一般滋味在心头。", timedate : "2021-11-16 20:37"},
    {id : "13",title: "忆秦娥·萧声咽 李白" , content : "箫声咽，秦娥梦断秦楼月。秦楼月，年年柳色，灞陵伤别。乐游原上清秋节，咸阳古道音尘绝。音尘绝，西风残照，汉家陵阙。", timedate : "2021-11-16 20:37"},
    {id : "12",title: "浪淘沙令·帘外雨潺潺 李煜" , content : "帘外雨潺潺，春意阑珊。罗衾不耐五更寒。梦里不知身是客，一晌贪欢。独自莫凭栏，无限江山。别时容易见时难。流水落花春去也，天上人间。", timedate : "2021-11-16 20:37"},
    {id : "11",title: "江城子·乙卯正月二十日夜记梦 苏轼" , content : "十年生死两茫茫，不思量，自难忘。千里孤坟，无处话凄凉。纵使相逢应不识，尘满面，鬓如霜。夜来幽梦忽还乡，小轩窗，正梳妆。相顾无言，惟有泪千行。料得年年肠断处，明月夜，短松冈。(肠断 一作：断肠)", timedate : "2021-11-16 20:37"},
    {id : "10",title: "六州歌头·少年侠气 贺铸" , content : "少年侠气，交结五都雄。肝胆洞，毛发耸。立谈中，死生同。一诺千金重。推翘勇，矜豪纵。轻盖拥，联飞鞚，斗城东。轰饮酒垆，春色浮寒瓮，吸海垂虹。闲呼鹰嗾犬，白羽摘雕弓，狡穴俄空。乐匆匆。(闲呼 一作：间呼)似黄粱梦，辞丹凤；明月共，漾孤蓬。官冗从，怀倥偬；落尘笼，簿书丛。鶡弁如云众，供粗用，忽奇功。笳鼓动，渔阳弄，思悲翁。不请长缨，系取天骄种，剑吼西风。恨登山临水，手寄七弦桐，目送归鸿。", timedate : "2021-11-16 20:37"},
    {id : "9",title: "贫女 秦韬玉" , content : "蓬门未识绮罗香，拟托良媒益自伤。谁爱风流高格调，共怜时世俭梳妆。敢将十指夸针巧，不把双眉斗画长。苦恨年年压金线，为他人作嫁衣裳。", timedate : "2021-11-16 20:37"},
    {id : "8",title: "左迁至蓝关示侄孙湘 韩愈" , content : "一封朝奏九重天，夕贬潮州路八千。欲为圣明除弊事，肯将衰朽惜残年！云横秦岭家何在？雪拥蓝关马不前。知汝远来应有意，好收吾骨瘴江边。", timedate : "2021-11-16 20:37"},
    {id : "7",title: "结客少年场行 虞世南" , content : "韩魏多奇节，倜傥遗声利。共矜然诺心，各负纵横志。结交一言重，相期千里至。绿沉明月弦，金络浮云辔。吹箫入吴市，击筑游燕肆。寻源博望侯，结客远相求。少年怀一顾，长驱背陇头。焰焰戈霜动，耿耿剑虹浮。天山冬夏雪，交河南北流。云起龙沙暗，木落雁门秋。轻生殉知己，非是为身谋。", timedate : "2021-11-16 20:37"},
    {id : "6",title: "节妇吟·寄东平李司空师道 张籍" , content : "君知妾有夫，赠妾双明珠。感君缠绵意，系在红罗襦。妾家高楼连苑起，良人执戟明光里。知君用心如日月，事夫誓拟同生死。还君明珠双泪垂，恨不相逢未嫁时。", timedate : "2021-11-16 20:37"},
    {id : "5",title: "不第后赋菊 黄巢" , content : "待到秋来九月八，我花开后百花杀。冲天香阵透长安，满城尽带黄金甲。", timedate : "2021-11-16 20:37"},
    {id : "4",title: "一剪梅·余赴广东实之夜饯于风亭 刘克庄" , content : "束缊宵行十里强。挑得诗囊，抛了衣囊。天寒路滑马蹄僵，元是王郎，来送刘郎。酒酣耳热说文章。惊倒邻墙，推倒胡床。旁观拍手笑疏狂。疏又何妨，狂又何妨？", timedate : "2021-11-16 20:37"},
    {id : "3",title: "桃花庵歌 唐寅" , content : "桃花坞里桃花庵，桃花庵里桃花仙。桃花仙人种桃树，又折花枝当酒钱。酒醒只在花前坐，酒醉还须花下眠。花前花後日复日，酒醉酒醒年复年。不愿鞠躬车马前，但愿老死花酒间。车尘马足贵者趣，酒盏花枝贫者缘。若将富贵比贫贱，一在平地一在天。若将贫贱比车马，他得驱驰我得闲。世人笑我忒疯颠，我咲世人看不穿。记得五陵豪杰墓，无酒无花锄作田。", timedate : "2021-11-16 20:37"},
    {id : "2",title: "侠客行 李白" , content : "赵客缦胡缨，吴钩霜雪明。银鞍照白马，飒沓如流星。十步杀一人，千里不留行。事了拂衣去，深藏身与名。闲过信陵饮，脱剑膝前横。将炙啖朱亥，持觞劝侯嬴。三杯吐然诺，五岳倒为轻。眼花耳热后，意气素霓生。救赵挥金槌，邯郸先震惊。千秋二壮士，烜赫大梁城。纵死侠骨香，不惭世上英。谁能书阁下，白首太玄经。", timedate : "2021-11-16 20:37"},
    {id : "1",title: "贺新郎·甚矣吾哀矣 辛弃疾" , content : "邑中园亭，仆皆为赋此词。一日，独坐停云，水声山色，竞来相娱。意溪山欲援例者，遂作数语，庶几仿佛渊明思亲友之意云。甚矣吾衰矣。怅平生、交游零落，只今余几！白发空垂三千丈，一笑人间万事。问何物、能令公喜？我见青山多妩媚，料青山见我应如是。情与貌，略相似。一尊搔首东窗里。想渊明、《停云》诗就，此时风味。江左沉酣求名者，岂识浊醪妙理。回首叫、云飞风起。不恨古人吾不见，恨古人不见吾狂耳。知我者，二三子。", timedate : "2021-11-16 20:37"},
    
]
